<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >
          <!-- 输入机器人编号 start -->
          <el-form-item label="机器人编号" prop="robotCode">
            <el-input
              class="input-name"
              v-model="searchValidateForm.robotCode"
              placeholder="请输入机器人编号"
            ></el-input>
          </el-form-item>
          <!-- 输入机器人编号 end -->

          <!-- 选择景区名称 start -->
          <el-form-item label="机器人状态" prop="spotId">
            <el-select
              v-model="searchValidateForm.spotId"
              filterable
              placeholder="全部"
              @change="onChangeSearchSpot"
              clearable
							style="width: 90px"
            >
              <el-option
                v-for="(item, index) in robotState"
                :key="index"
                :label="item.value"
                :value="item.state"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 选择景区名称 end -->

          <!-- 输入申请起止时间 start -->
          <el-form-item label="" prop="startEndDate" >
            <el-date-picker
              v-model="startEndDate"
              unlink-panels
              :type="dateType"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="yyyy-MM-dd"
							style="width: 300px"
            >
            </el-date-picker>
          </el-form-item>
          <!-- 输入申请起止时间 end -->
        </el-form>

        <!-- 搜索 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="info" icon="el-icon-refresh-right" @click="onReset">重置</el-button>
        <!-- 重置 end -->

				<!--考勤管理时间-->
				<!-- <el-button type="primary" @click="centerDialogVisible = true">考勤管理时间</el-button> -->
	
        <!-- 新增 start -->
        <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">添加</el-button>
        <!-- 新增 end -->

        <!-- 导出 Excel start -->

        <!-- <download-excel class="export-btn" :data="tableData" :fields="tableFields" type="xls" header="合作公司" name="合作公司.xls">
          <el-button type="primary" icon="el-icon-upload">
            导出Excel
          </el-button>
        </download-excel> -->
        <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-download"
          @click="exportExcel"
          >导出Excel</el-button>

        <!-- 导出 Excel end -->

        <!-- 导入 Excel start -->
        <!-- <el-upload class="upload-demo" action="" :on-change="handleChange" :on-exceed="handleExceed" :on-remove="handleRemove" :file-list="fileListUpload" :show-file-list="false" :limit="limitUpload" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel" :auto-upload="false" v-if="updateUpload">
          <el-button type="primary" icon="el-icon-upload">导入Excel</el-button>
        </el-upload> -->
        <!-- 导入 Excel end -->
      </el-header>
      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="loadingTable"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="150">
            <template slot-scope="scope">
							<el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="primary" size="small">
							  修改版本号
							</el-button>
              <el-button @click.native.prevent="deleteRow(scope.$index, scope.row)" type="info" size="small">
                删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40, 50, 60, 70, 80, 90]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <el-dialog
        class="dialog"
        :title="dialogTitle"
        :visible.sync="dialogVisible"
        :before-close="handleClose"
        :close-on-click-modal="false"
				width="30%"
      >
        <!-- form表单 -->
        <el-form
          :model="validateForm"
          ref="validateForm"
          label-width="100px"
          class="demo-ruleForm"
          :rules="validateFormRules"
        >
				
				<!-- 机器人编号 start -->
				<el-form-item label="机器人编号" prop="robotCode">
				  <el-input
						type="text"
				    v-model="validateForm.robotCode"
				    autocomplete="off"
				    placeholder="请输入机器人编号"
				  ></el-input>
				</el-form-item>
				<!-- 机器人编号 end -->	
				
          <!-- 景区名称 start -->
          <el-form-item label="请选择时间" prop="robotStartTime" v-if="robotStartTimeBool">
						<el-date-picker
								v-model="robotStartTime"
								type="datetime"
								placeholder="选择日期时间"
								style="width: 270px;">
							</el-date-picker>
          </el-form-item>
          <!-- 景区名称 end -->

          <!-- 机器人点位 start -->
          <el-form-item label="机器人点位" prop="robotPosition" v-if="robotPositionBool">
            <el-input
              type="text"
              v-model="validateForm.robotPosition"
              autocomplete="off"
              placeholder="请输入机器人点位"
            ></el-input>
          </el-form-item>
          <!-- 机器人点位 end -->
					
					<!-- 机器人版本号 start -->
					<el-form-item label="机器人版本号" prop="clientVersion" v-if="clientVersionBool">
					  <el-input
					    type="text"
					    v-model="validateForm.clientVersion"
					    autocomplete="off"
					    placeholder="请输入机器人点位"
					  ></el-input>
					</el-form-item>
					<!-- 机器人版本号 end -->
					
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')"
            >提交</el-button
          >
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->
			<!--考勤管理时间-->
			<el-dialog
			  title="考勤管理时间"
			  :visible.sync="centerDialogVisible"
				 width="40%"
			  center>
				<template>
				  <el-time-select
				    placeholder="请输入开始时间"
				    v-model="startTime"
						@change="handStartTime"
				    :picker-options="{
				      start: '08:00',
				      step: '00:10',
				      end: '20:00'
				    }">
				  </el-time-select>
				  <el-time-select
				    placeholder="请输入结束时间"
				    v-model="endTime"
						@change="handEndTime"
				    :picker-options="{
				      start: '08:00',							
				      step: '00:10',
				      end: '20:00',
				      minTime: startTime
				    }">
				  </el-time-select>
				</template>
				<template>
					<div class="dialogItem">
						<div  v-for="(item , index) in clockItem" :key="index">
							<div class="clockItemcss">
								<i class="el-icon-remove" @click="iconRemove(index)"></i>
								<el-button type="primary">{{item.startTime}}-{{item.endTime}}</el-button>
							</div>
						</div>
					</div>
				</template>
			  <span slot="footer" class="dialog-footer">
					<!-- <el-button slot="reference">删除</el-button> -->
			    <el-button @click="centerDialogVisible = false">取 消</el-button>
			    <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
			  </span>
			</el-dialog>
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
import { formatDateTime } from '../utils/utils.js'
export default {
  name: 'PartnerCompany',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }
		let checkInt = (rule, value, callback) => {
		      if (Number(value) && value % 1 === 0 && value >= 0) {
		        // console.log(value);
		        callback();
		      } else {
		        return callback(new Error("请输入正整数！"));
		      }
		    };
    return {
      // input: '', // 请输入公司名称
      searchCompanyName: '', // 搜索的公司名称
      tableFields: {
        //导出Excel表格的表头设置
        所属景区: 'scenicSpotName',
        开始时间: 'startDate',
        结束时间: 'endDate',
        机器人编号: 'robotCode',
        机器人状态: 'robotStatus',
		故障状态:'errorRecordsAffect',
        机器人点位: 'robotPosition',
        机器人版本号: 'clientVersion',
      },
      tableData: [
				// {
				// 	scenicSpotName :'123',
				// 	startDate:'123',
				// 	endDate:'123',
				// 	robotCode :'123',
				// 	robotState:'123',
				// 	robotPosition:'123',
				// 	robotV:'123'
				// }
			], // 表格数据
      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画

      loadingTable: false,

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数
			robotStartTime:[],//添加当前时间
      searchScenicOptions: [
        // {
        //   spotId: 1,
        //   spotName: '测试景区1'
        // },
        // {
        //   spotId: 2,
        //   spotName: '测试景区2'
        // },
        // {
        //   spotId: 3,
        //   spotName: '测试景区3'
        // }
      ], // 搜索时景区选项

      searchUserOptions: [
        {
          peopleId: 1,
          peopleName: '测试维修人员1'
        },
        {
          peopleId: 2,
          peopleName: '测试维修人员2'
        },
        {
          peopleId: 3,
          peopleName: '测试维修人员3'
        }
      ], // 搜索时维修人员选项
			//机器人状态
			robotState:[
				{
					state:0,
					value:'关机'
				},
				{
					state:1,
					value:'开机'
				}
			],
			clientVersionBool:false,//机器人版本号
			robotPositionBool:false,//机器人点位
			robotStartTimeBool:false,//机器人时间
      dialogVisible: false, // 是否显示dialog对话框
      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'
			centerDialogVisible: false,//对话框开关
      dateType: 'daterange', // 日期类型

      startEndDate: [], // 搜索表单起止时间
      searchValidateForm: {
        // robotCode: '', // 机器人编号
        // spotId: '', // 景区id
        // spotName: '', // 景区名称
        // faultId: '', //故障id
        // faultName: '', // 故障名称
        // faultNumber: '', // 故障单号
        // peopleId: '', // 维修人员id
        // peopleName: '', // 维修人员名称
        // startEndDate: '' // 派单起止时间
      }, // 搜索表单验证对象
      validateForm: {},
      total: 0,
			clockItem:[{
				index:1,
				startTime:'09:00',
				endTime:'09:10'
			}],//查询打卡时间
      dialogTitle: '',
			startTime: '',//开始时间
			endTime: '',//结束时间
      limitUpload: 3,
      file: null, // 上传的Excel文件
      fileTemp: null, // 上传的临时Excel文件
      fileListUpload: [], // 上传Excel文件列表

      updateUpload: true, // 是否显示upload组件

      // 提交表单验证规则
      validateFormRules: {
				// let checkInt = (rule, value, callback) => {
				//       if (Number(value) && value % 1 === 0 && value >= 0) {
				//         console.log(value);
				//         callback();
				//       } else {
				//         return callback(new Error("请输入正整数！"));
				//       }
				//     };
        // 机器人编号
        robotCode: [
          {
            validator: checkInt,
            required: true,
            trigger: ['blur', 'change']
          }
        ],
				// 机器人时间
				// robotStartTime: [
				//   {
				//     validator: checkRequireMethod('机器人时间'),
				//     required: true,
				//     trigger: ['blur', 'change']
				//   }
				// ]
      },

      // 搜索表单验证规则
      searchValidateFormRules: {},
			selectTiemState:'0'
    }
  },
  created() {
    // 如果当前环境是开发环境才调用登录方法
    if (process.env.NODE_ENV == 'development') {
      this.login()
    }

    this.getSearchValidateForm()
    this.getValidateForm()
    this.initTableData()
    // this.getScenicList()
    this.getSysAppUsers()
  },
  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        ...validateForm
      }
    },

    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=houjingchen&password=houjingchen'
      )
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      // console.log(this.searchValidateForm)
      this.loadingTable = true
      const { data: res } = await this.$http.get(
        '/sys/AttendanceRecord/RobotAttendance',
        {
          params: {
            ...this.searchValidateForm,
            startDate: this.startEndDate[0],
            endDate: this.startEndDate[1],
						robotStatus:this.searchValidateForm.spotId,
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
			console.log(res)
      if (res.state == '200') {
        this.tableData = res.data
				// console.log(this.tableData[0].startDate)
        this.total = res.total
        this.loadingTable = false
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList() {
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/spotList'
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchScenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
			console.log(this.startEndDate)
			var data = {
					startDate:this.startEndDate[0],
					endDate:this.startEndDate[1],
					robotStatus:this.searchValidateForm.spotId
			}
      const { data: res } = await this.$http.post(
        '/sys/AttendanceRecord/exportRobotAttendance',this.$qs.stringify(data),
        {
          responseType: 'arraybuffer'
        }
      )
      // console.log(res)
      const url = window.URL.createObjectURL(
        new Blob([res], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        })
      )
      const a = document.createElement('a')
      // console.log(url)
      a.href = url
      a.download = '机器人考勤'
      a.click()
      this.downloadLoading = false
    },
		
		// 点击删除时触发
		 deleteRow(index, row) {
		   console.log(index, row.recordId);
		   this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
		     confirmButtonText: '确定',
		     cancelButtonText: '取消',
		     type: 'warning'
		   })
		     .then(async () => {
		       const { data: res } = await this.$http.get(
		         '/sys/AttendanceRecord/deleteRobotAttendance',
		         {
		           params: {
		             recordId: row.recordId
		           }
		         }
		       )
		       console.log(res);
		       if (res.state == '200') {
		         this.$message({
		           type: 'success',
		           message: res.msg,
		           offset: 100,
		           center: true,
		           duration: 1000
		         })
		         this.initTableData()
		       } else {
		         this.$message({
		           type: 'error',
		           // message: res.msg,
							 message: '关机状态下不能删除',
		           offset: 100,
		           center: true,
		           duration: 3000
		         })
		       }
		     })
		     .catch(() => {
		       this.$message({
		         type: 'info',
		         message: '已取消删除',
		         offset: 100,
		         center: true,
		         duration: 1000
		       })
		     })
		 },
    /**
     * 获取维修人员列表
     */
    async getSysAppUsers() {
      const { data: res } = await this.$http.get(
        '/system/appUsers/getSysAppUsers'
      )
      this.searchUserOptions = res.data
    },

    // 点击搜索按钮时触发
    onSearch(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.initTableData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 重置
    onReset() {
      // 清空所有输入框内容
      this.getSearchValidateForm()
      this.getValidateForm()

      this.startEndDate = []

      this.dateType = 'daterange'

      // 重新获取表格数据
      this.initTableData()
    },
			
			iconRemove(index){
				console.log(index)
				// this.dialogItem = false
				this.clockItem.splice(index,1);
				// this.clockItem = this.clockItem.filter((item) =>{
				// 	return item.id !== item.id;
				// })
				// console.log(this.clockItem)
				// // var newar = this.clockItem.filter(item=>)
				// console.log(newar)
				// return newar
				// this.clockItem  = newar
				// console.log(v)
			},
			
    // 导出Excel表格
    exportExcel() {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true
          await this.getExportTableData()
          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.downloadLoading = false
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 新增
    onAdd() {
      this.dialogVisible = true
			console.log(this.validateForm)
      this.dialogState = 'add'
      this.dialogTitle = '添加机器人版本号'
			this.clientVersionBool = false
			this.robotPositionBool = true
			this.robotStartTimeBool= true
    },

    // 点击编辑时触发
    editRow(index, row) {
      // console.log(index, row);
      this.dialogVisible = true // 是否显示编辑dialog对话框
      var rowObj = Object.assign({}, row) // 深拷贝row对象
      // rowObj.collectionAccountNumber = parseFloat(rowObj.collectionAccountNumber)
      this.validateForm = rowObj
      this.dialogState = 'edit'
      this.dialogTitle = '编辑机器人版本号'
			this.clientVersionBool = true
			this.robotPositionBool = false
			this.robotStartTimeBool= false
    },

    // 编辑dialog取消
    dialogClose() {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 编辑dialog 右上角关闭按钮
    handleClose(done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // 搜索时选项变化 start
    /**
     * 景区选项变化时触发
     */
    async onChangeSearchSpot(spotId) {
      this.searchScenicOptions.forEach((item) => {
        if (item.SCENIC_SPOT_ID == spotId) {
          this.searchValidateForm.spotName = item.SCENIC_SPOT_NAME
        }
      })
    },

    /**
     * 故障选项变化时触发
     */
    async onChangeSearchUser(userId) {
      this.searchUserOptions.forEach((item) => {
        if (item.userId == userId) {
          this.searchValidateForm.userName = item.userName
        }
      })
    },
    // 搜索时选项变化 end

//获取开始时间
			handStartTime(evet){
				if(evet != null){
					this.startTime = evet
				}
			},
			//获取结束时间
			handEndTime(evet){
				if(evet != null){
						// this.endTime = ''
						// this.endTime = evet
						console.log( this.startTime)
						var startTime = this.startTime
						var endTime = this.endTime
						var arr = {
							index:this.index++,
							startTime:startTime,
							endTime:endTime
						}
						console.log(arr)
							if(arr.startTime != '' && arr.startTime != null){
								this.clockItem.push(arr)
							}
						}
						// console.log(this.endTime)
				// console.log(this.endTime)
				
			},

    // dialog提交
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (this.dialogState == 'add') {
            // 新增机器人版本号
            console.log('新增机器人列表')
            this.addTableData(this.validateForm)
          } else if (this.dialogState == 'edit') {
            // 修改机器人版本号
						this.clientVersionBool = true
            console.log('修改机器人版本号')
            this.editTableData(this.validateForm)
          }
          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单
					this.getValidateForm()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange(currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange(pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    /**
     * 更新upload组件的方法
     */
    reloadUpload() {
      // this.$forceUpdate()
      // 移除组件
      this.updateUpload = false
      // 在组件移除后，重新渲染组件
      // this.$nextTick可实现在DOM 状态更新后，执行传入的方法。
      this.$nextTick(() => {
        this.updateUpload = true
      })
    },

    // 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
    handleChange(file, fileList) {
      // console.log(file);
      this.fileListUpload[0] = file
      this.fileTemp = file.raw

      this.reloadUpload()

      // 导入判断
      if (this.fileTemp) {
        if (
          this.fileTemp.type ==
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
          this.fileTemp.type == 'application/vnd.ms-excel'
        ) {
          this.importfxx(this.fileTemp)
        } else {
          this.$message({
            type: 'warning',
            message: '附件格式错误，请删除后重新上传！'
          })
        }
      } else {
        this.$message({
          type: 'warning',
          message: '请上传附件！'
        })
      }
    },

    // 文件超出个数限制时的钩子
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      )
    },

    // 文件列表移除文件时的钩子
    handleRemove(file, fileList) {
      this.fileTemp = null
      this.fileListUpload = []
    },

		/**
		 * 新增表格数据
		 */
		async addTableData (data) {
			var startTime = null 
			startTime = formatDateTime(
			  this.robotStartTime,
			  this.selectTiemState
			)
			console.log(startTime)
		  const { data: res } = await this.$http.post(
		    '/sys/AttendanceRecord/insertRobotAttendance',this.$qs.stringify(data)
				// {
				// 	params:{
				// 		robotStartTime:startTime,
				// 		robotCode:this.validateForm.robotCode,
				// 		robotPosition:this.validateForm.robotPosition
				// 	}
				// }
		  )
			console.log(res)
		  if (res.state == '200') {
		    this.$message({
		      message: res.msg,
		      type: 'success',
		      offset: 100,
		      center: true,
		      duration: 1000
		    })
		    this.initTableData()
		    this.$refs.validateForm.resetFields() // 重置表单
		  } else {
				console.log(res.msg)
		    this.$message({
		      message: res.msg,
		      type: 'error',
		      offset: 100,
		      center: true,
		      duration: 3000
		    })
		  }
		},
		
		/**
		 * 修改表格数据
		 */
		async editTableData (data) {
		  const { data: res } = await this.$http.post(
		    '/sys/AttendanceRecord/updateRobotAttendance',this.$qs.stringify(data)
		  )
			console.log(res)
		  if (res.state == '200') {
		    this.$message({
		      message: res.msg,
		      type: 'success',
		      offset: 100,
		      center: true,
		      duration: 1000
		    })
		    this.initTableData()
		  } else {
		    this.$message({
		      message: res.msg,
		      type: 'error',
		      offset: 100,
		      center: true,
		      duration: 3000
		    })
		  }
		},
		
    // 导入格式
    importfxx(obj) {
      let _this = this
      // 通过DOM取文件数据
      this.file = obj
      var rABS = false //是否将文件读取为二进制字符串
      var f = this.file
      var reader = new FileReader()
      //if (!FileReader.prototype.readAsBinaryString) {
      FileReader.prototype.readAsBinaryString = function(f) {
        var binary = ''
        var rABS = false //是否将文件读取为二进制字符串
        var pt = this
        var wb //读取完成的数据
        var outdata
        var reader = new FileReader()
        reader.onload = function(e) {
          var bytes = new Uint8Array(reader.result)
          var length = bytes.byteLength
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i])
          }
          var XLSX = require('xlsx')
          if (rABS) {
            wb = XLSX.read(btoa(fixdata(binary)), {
              //手动转化
              type: 'base64'
            })
          } else {
            wb = XLSX.read(binary, {
              type: 'binary'
            })
          }
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]) //outdata就是你想要的东西
          console.log(outdata)
          this.da = [...outdata]
          let arr = []
          this.da.map((v) => {
            let obj = {
              robotCount: v['机器人数量'],
              type: v['发货进度'],
              applicantName: v['申请人'],
              factoryName: v['发货工厂'],
              consignorName: v['发货人'],
              receivingName: v['收货景区'],
              consigneeName: v['收货人'],
              consigneePhone: v['收货人电话'],
              receiningAddress: v['收货地址'],
              consignmentDate: v['发货时间']
            }
            arr.push(obj)
          })
          // // 将 arr 赋值给 tableData，来更新表格数据
          // this.tableData = arr
          return arr
        }
        reader.readAsArrayBuffer(f)
      }

      if (rABS) {
        reader.readAsArrayBuffer(f)
      } else {
        reader.readAsBinaryString(f)
      }
    }
  },
  computed: {
    tableContentValue() {
      return (row, value) => {
			console.log(row)
        var result = ''
        if (value == 'endDate') {
          result = row.endDate.substring(0, 10)
        } else if(value == 'startDate') {
          result = row.startDate.substring(0, 10)
        } else if(value == 'errorRecordsAffect'){
			if(row.errorRecordsAffect == 0){
				result = '维修'
			}else if(row.errorRecordsAffect == 1){
				result = '故障'
			}else{
				result = '正常'
			}
		}else {
          result = row[value]
        }
        return result
      }
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  }
}
</script>

