/**
 * 格式化月份（2021-09）
 */
export function formatMonth() {
  var year = new Date().getFullYear().toString();
  var month = (new Date().getMonth() + 1).toString().padStart(2, "0");
  var currentMonth = year + "-" + month;
  return currentMonth;
}

/**
 * 格式化日份（2021年9月17日）
 */
export function formatDate() {
  var year = new Date().getFullYear().toString();
  var month = (new Date().getMonth() + 1).toString().padStart(2, "0");
  var date = new Date()
    .getDate()
    .toString()
    .padStart(2, "0");
  var currentDate = year + "年" + month + "月" + date + "日";
  return currentDate;
}

/**
 * 转换时间（2021-9-1 00:00:00）
 */
export function formatDateTime (datetime, type = "0") {
  var year = datetime.getFullYear().toString();
  var month = (datetime.getMonth() + 1).toString().padStart(2, "0");
  var date = datetime
    .getDate()
    .toString()
    .padStart(2, "0");
			var hours = datetime.getHours().toString().padStart(2, "0");
			var minutes = datetime.getMinutes().toString().padStart(2, "0");
			var seconds = datetime.getSeconds().toString().padStart(2, "0");
  var currentDate = "";
  if (type == "0") {
    // 格式化为日
    currentDate = year + "-" + month + "-" + date + ' '+hours +":" +  minutes +":" + seconds;
  } else if (type == "2") {
    // 格式化为月
    currentDate = year + "-" + month;
  } else if (type == "1") {
    // 格式化为年
    currentDate = year;
  }
  return currentDate;
}


/**
 * 生成一个带参数的url
 */
export function createUrl(obj) {
    var length = obj && obj.length,
        idx = 0,
        url = obj.url + '?';
    for (var key in obj) {
        if (key != 'url' && obj[key] !== null) {
            url += (key + '=' + encodeURIComponent(obj[key]) + '&');
        }
    }
    var result = ""
    if(url.lastIndexOf('&') > -1) {
      result = url.substring(0, url.lastIndexOf('&'))
    }
    if(url.lastIndexOf('?') > -1) {
      result = url.substring(0, url.lastIndexOf('?'))
    }
    return result;
}